import React, {Component, Fragment} from 'react';
import { Responsive, Segment, Button, Icon, Menu, Grid, Form, TextArea, Container,Embed, Divider, Image, Input } from 'semantic-ui-react';
import '../App.css';
import { HashLink as Link } from 'react-router-hash-link';


const DataLabeling = (props)=>{
	const browserLang = props.browserLang;
	const spanish = browserLang.indexOf("es") > -1;
	const imgUrl = "images/background-image-hero.jpg";
	let backgroundImg = `linear-gradient(45deg,  rgba(0,26,255,.7) 1%,rgba(98,38,183,.7) 30%,rgba(140,40,99,.7) 69%,rgba(250,34,52,.7) 100%),url(${imgUrl})`;
	return(
		<header className="App-hero" style={{
			"minHeight":"100vh",
			"backgroundImage":backgroundImg,
			"color":"white"
		}}>
        
		<Segment basic style={{width:"70%","zIndex":"10"}} className="cta-hero-section">
		<Grid stackable>
			<Grid.Row columns={2}>
				<Grid.Column style={{"textAlign":"left"}}>
					<h1 style={{"textTransform": "uppercase"}}>
			{spanish ? (
				"Etiquetado de Datos"
				):
				"Data labeling"}
			
			</h1>
			<br/>
			<br/>
			<h2 style={{"lineHeight":"1.5"}}>{spanish ? 
				"Los algoritmos de aprendizaje sólo serán tan buenos como los datos usados para entrenarlos. Nosotros le ayudamos con el proceso de etiquetado para que usted sólo se preocupe de entrenar sus modelos." : 
				"A learning model will only be as good as the data used to train it. Our expert team will help you label your data so you only need to worry about training and using your model"}</h2>
			<br/>
		
			
				</Grid.Column>
				<Grid.Column>
					<Segment>
						<Form action="https://formspree.io/engin.ai.solutions@gmail.com" method="POST" style={{"color":"black"}}>
							<h2>
								{spanish? "Contáctenos para Hacer un Demo"
									:
									"Request a Demo"
								}
							</h2>
							<Segment basic style={{"textAlign":"left"}}>
							<Form.Field>
								<label htmlFor="name">Name</label>	
								<Input name="name" placeholder="John Doe">
								</Input>
							</Form.Field>
							<Form.Field>
								<label htmlFor="name">Email</label>	
								<Input name="name" placeholder="jdoe@example.com">
								</Input>
							</Form.Field>
							<Form.Field>
								<label htmlFor="name">Company</label>	
								<Input name="name" placeholder="My Business">
								</Input>
							</Form.Field>
							<Form.Field>
								<label htmlFor="name">Comment</label>	
								<TextArea name="name">
									
								</TextArea>
								
							</Form.Field>
							</Segment>
							
								<Button type="submit" className="mainCTABtn" inverted color="red" size="huge" syle={{"width":"50%"}} >
								{spanish?
									<span className="buttonText">Contáctenos</span> 
									 : 
									<span>Contact Us</span>}
								<span className ="iconContainer" style={{"display":"inline-block", "width":"10px"}}>
									<Icon name="chevron right">
										
									</Icon>
								</span>
							</Button>
						</Form>
					</Segment>
				</Grid.Column>
			</Grid.Row>
		</Grid>
			
					<Link to="/#contact-us">
				

			</Link>
		</Segment>

      </header>
		);	
} 

export default DataLabeling;