import React, { Component, Fragment } from 'react';
import { Menu, Segment, Image, Responsive,Icon } from 'semantic-ui-react';
import Logo from "./images/white-logo-plus-name-final-01.svg";
import Burger from "./images/burger-icon-01.svg";
import './App.css';
import { HashLink as Link } from 'react-router-hash-link';
import { disableBodyScroll, enableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock';
import { HashRouter as Router , Switch, Route, withRouter, Redirect } from "react-router-dom";

class Navbar extends Component {
  constructor(props){
    super(props);
    this.state = {
        mobileNav : false,

      }
     
  }
  targetElement = null;
  



   componentDidMount() {
    // 2. Get a target element that you want to persist scrolling for (such as a modal/lightbox/flyout/nav). 
    // Specifically, the target element is the one we would like to allow scroll on (NOT a parent of that element).
    // This is also the element to apply the CSS '-webkit-overflow-scrolling: touch;' if desired.
    this.targetElement = document.querySelector('.mobile-overlay');
  }
 componentDidUpdate(prevProps) {
    if (this.props.location !== prevProps.location) {
      this.onRouteChanged();
    }
  }

  onRouteChanged() {
    
    this.setState({mobileNav:false});
    clearAllBodyScrollLocks();
  }
  handleItemClick = (e, { name }) => this.setState({ activeItem: name })
  navbarOn = ()=>{
    let mobile = this.state.mobileNav;
    //console.log(mobile);
    
    
    this.setState({mobileNav:!mobile});
    mobile = !mobile;
    if(mobile){
      disableBodyScroll(this.targetElement);  
    }else{
      clearAllBodyScrollLocks();
    }
  }

  onLinkClick = (e)=>{
    console.log(e.target.name);
    console.log("clicked");
    console.log(e);

    e.preventDefault();
    let l = e.target.name;
    clearAllBodyScrollLocks();  
    this.setState({mobileNav:false});
    this.props.history.push(l);



  }

  render() {
    const { activeItem } = this.state
    const browserLang = this.props.browserLang;
    let mobileNav = this.state.mobileNav;
    let menuOpen = mobileNav? "open" : "closed";
    let mobileClassname = "mobile-overlay-closed";
    let menuItemClassName = `mobile-menu-item ${menuOpen}`;
    if(mobileNav){
      mobileClassname = "mobile-overlay-open";

    }
    return (
      <Fragment>
        <Responsive {...Responsive.onlyComputer}>
        <Segment className="App-nav" inverted style={{margin:"0",position:"absolute","width":"100%", "zIndex":"100"}}>
        <Menu inverted secondary>
        <Menu.Item>
        <a href="/">
          <Image size="small" src={Logo}>
          </Image>
        </a>
       
          

        </Menu.Item>         
          
        </Menu>
      </Segment>
       </Responsive>
       <Responsive {...Responsive.onlyMobile}>
       <div style={{"overflow":"hidden","maxHeight":"100vh","zIndex":"200"}}>
         <div className={`mobile-overlay ${mobileClassname}`} style={{"maxHeight":"150vh"}}></div>
       {mobileNav ? 
        <Fragment>
          
        <div style={{"height":"100vh","zIndex":"120", "position":"absolute","width":"100%"}}>
         <Segment className="App-nav" inverted >
        <Menu inverted secondary>
          <Menu.Item>
          
            <Image onClick={this.onLinkClick} name="/#" size="small" src={Logo}>
              
            </Image>
            
            
          </Menu.Item>
          <Menu.Menu position="right">
            <Menu.Item>
               <Image onClick={this.navbarOn} style={{"width":"38px", "height":"29px"}} size="tiny" src={Burger}/>
            </Menu.Item>
          </Menu.Menu>
        </Menu>

        <Menu vertical inverted className="menu-item-group">
          <Menu.Item className={`mobile-menu-item ${menuOpen}`} href="#/data-labeling" >
            Data Labeling
          </Menu.Item>
          <Menu.Item className={`mobile-menu-item ${menuOpen}`} href="#/ai-consulting" >
            AI Consulting
          </Menu.Item>
          <Menu.Item className={`mobile-menu-item ${menuOpen}`} href="#/pavements-analysis" >
            Pavement Damage Analysis
          </Menu.Item>
          <Menu.Item className={`mobile-menu-item ${menuOpen}`}>
          <Link to="/#contact-us">
            Contact Us
          </Link>
            
          </Menu.Item>
        </Menu>

        
         
      </Segment>
       </div>
        </Fragment>
        
          : null
       }
       </div>
       
       
        <Segment className="App-nav" inverted style={{margin:"0",position:"absolute","width":"100%","zIndex":"100"}}>
        <Menu inverted secondary>
          <Menu.Item>
          <Link to="/#">
            <Image size="small" src={Logo}>
              
            </Image>
          </Link>
            
            
          </Menu.Item>
          <Menu.Menu position="right">
            <Menu.Item>
               <Image onClick={this.navbarOn} style={{"width":"38px", "height":"29px"}} size="tiny" src={Burger}/>
            </Menu.Item>
          </Menu.Menu>
        </Menu>
        
       
      </Segment>
      

       </Responsive>
      </Fragment>
       
      
    )
  }
}

export default withRouter(Navbar);